.coca-image-picker {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	gap: 10px;

	.picker-item {
		position: relative;
		display: inline-block;
		margin-right: 0 !important;
		line-height: 0;
		cursor: pointer;
		border: 2px solid #eee;
		transition: 300ms ease-in-out;

		&.active {
			border: 2px solid #2271b1;
			box-shadow: 0 0 4px 0 #2271b1;
		}

		&:hover:not(.active) {
			box-shadow: 0 0 10px 0 #eee;
		}

		&:hover input,
		&.active input {
			outline: 2px solid transparent;
			box-shadow: 0 0 0 1px #2271b1;
		}

		&:not(.active) input[type="button"],
		&:not(.active) input[type="checkbox"], {
			opacity: 0;
		}

		input[type="checkbox"],
		input[type="button"] {
			position: absolute;
			z-index: 10;
			margin: 0;
			border: none;
			border-radius: unset;
			transition: 300ms ease-in-out;

			&[value] {
				display: none;
			}
		}

		img {
			position: relative;
			max-width: 100px;
			height: 100px;
		}
	}
}
